body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}

.App {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  margin: 5px;
}

h1 {
  color: white;
  font-size: 45px;
  position: absolute;
  line-height: 1;
}

.container {
  margin: auto;
  width: 100%;
  max-width: 590px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.container .card {
  width: 100%;
  max-width: 100px;
  margin: 10px 0;
  text-align: center;
}
.container .card p {
  font-weight: 600;
}

.container .card .box {
  width: 100%;
  height: 100px;
  border-radius: 12px;
  cursor: pointer;
}
